import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom";
import Home from "./Home";
import './styles.css';
import breloom from "./breloom.png";

function App() {
  return (
    <div>
      <image src={breloom} id="homebutton" />
      <Router>
        <div>
          <Switch>
            <Route path="/">
              <Home />
            </Route>
            <Route
            path="/h"
            component={ Redirect }
            loc="https://hahs.sentral.com.au/portal2/"
            />
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;